<template>
	<view class="wrapper padding">
		<view class="content">
			<view class="list" v-if="!empty">
				<view class="item" v-for="(litt,idx) in dataInfo.orderList" :key="idx">
					<view class="flex-row-between row">
						<view class="title">{{litt.carTypeName}}</view>
						<view class="go" @click="goDeal(litt)">
							<view class="btn" v-if="!litt.selected">
								<button class="cu-btn block">关联</button>
							</view>
							<view class="txt" v-else>当前关联</view>
						</view>
					</view>
					
					<view class="flex-row-start row">
						<view class='key'>意向车型</view>
						<view class='txt want'>{{litt.carTypeName}}</view>
					</view>
					
					<view class="flex-row-start row">
						<view class="key">询价情况</view>
						<view class="value">{{litt.inquiryInfo!=null?litt.inquiryInfo:'- -'}}万</view>
					</view>
				</view>
				<loadMore :status="status" :content-text="textDesc"></loadMore>
			</view>
			<view class="no_data" v-else>
				暂无数据
			</view>
		</view>
	</view>
</template>

<script>
	import simpleTab from "../../components/simpleTab/simpleTab.vue"
	import request from "../../common/request.js"
	import {formatTime} from "../../common/tools.js"
	import loadMore from "../../components/loadMore/loadMore.vue"
	export default {
		data() {
			return {
				textDesc:{contentdown: "加载更多",contentrefresh: "正在加载...",contentnomore: "没有更多数据了"},
				status:"loading",
				empty:false,
				dataInfo:{},
				selectId:''
			};
		},
		methods:{
			scrollBottom(){
				if(this.status!="noMore"){
					this.getList()
				}
			},
			tel(tel){
				uni.showModal({
					title:"提示",
					content:"是否确认拨打电话",
					success:(res)=>{
						if(res.confirm){
							uni.makePhoneCall({
							  phoneNumber:tel //仅为示例，并非真实的电话号码
							})
						}
					}
				})
			},
			goDeal(item){
				var pages = getCurrentPages();
				var prevPage = pages[pages.length - 2];
				prevPage.$vm.relativeOrderInfo=item
				uni.navigateBack({
					delta:1
				})
			},
			
			getList(){
				this.request("/feedBack/relationOrderList").then((res)=>{
					if(res.resultCode==0){
						let _data=res.data
						if(_data && _data.orderList.length>0){ //如果有数据
							this.empty=false
							for(let i=0;i<_data.orderList.length;i++){
								if(_data.orderList[i].orderId==this.selectId){
									_data.orderList[i].selected=true;
									break
								}
							}
							this.dataInfo=_data
						}else{ //如果这次没数据
							this.empty=true
						}
						this.status="noMore"
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
					uni.stopPullDownRefresh()
				})
			},
		},
		onShow(){
			this.getList()
		},
		onLoad(params){
			this.selectId=params.orderId
		},
		components:{
			loadMore
		},
		onShareAppMessage(){
			return {
				title:'卫蓝购',//String	是	分享标题	
				path:'/pages/infomation/index',//String	是	页面 path ，必须是以 / 开头的完整路径。	QQ小程序不支持
				imageUrl:"/static/share.png"
			}
		},
	}
</script>
<style lang="stylus" scoped>
	.wrapper
		.content
			.list
				&:before
					content:''
					display:table
				.item
					margin-top:30upx
					padding:20upx
					background:rgba(248,249,252,1);
					font-size:24upx
					border-radius:8upx;
					.row
						&:not(:first-child)
							margin-top:15upx
						.key
							width:125upx
						.title
							font-size:32upx;
							font-family:PingFangSC-Medium,PingFang SC;
							font-weight:bold;
							color:rgba(31,35,52,1);
						.go
							.btn
								width:130upx;
								height:64upx;
								font-size:28upx;
								border-radius:8upx;
								button
									background:rgba(31,35,52,1);
									color:#fff
								&.disabled
									background:#8491A8
							.txt
								width:112upx;
								height:40upx;
								font-size:28upx;
								font-weight:400;
								color:rgba(178,182,192,1);
								line-height:40upx;
						.txt
							color:#000000
							&.desc
								margin-right:20upx
							&.want
								margin-right:20upx
</style>	

